<template>
  <div :class='prefixCls'>
    <head-top :go-back='true' title='' :append='true'>
      <button slot='prepend' :class="`${prefixCls}-btn`" @click='goBack'></button>
      <button slot='append' :class="`${prefixCls}-btn`" :disabled="disabled" @click='postFeed'></button>
    </head-top>
    <div></div>
    <div :class="`${prefixCls}-content`">
      <template v-if='!groupID || selectGroup.id'>
        <div :class="`${prefixCls}-select`" @click.stop='showChoosePage'>
          <div :class="`${prefixCls}-select-label`">{{ chooseTips }}</div>
          <v-icon type='base-arrow-r' />
        </div>
      </template>
      <div :class="`${prefixCls}-title`">
        <input type="text" v-model='title' placeholder=', 20' maxlength="20">
      </div>
      <div :class="`${prefixCls}-body`">
        <textarea v-txtautosize v-model='body' placeholder=", " />
      </div>
    </div>
    <ChooseGroup @on-close='closeChoosePage' v-model='selectGroup' v-show='showChoose' />
  </div>
</template>
<script>
import HeadTop from '@/components/HeadTop'
import ChooseGroup from './children/chooseGroup'
const prefixCls = 'post--group-feed'
export default {
  name: 'postGroupFeed',
  components: {
    HeadTop,
    ChooseGroup
  },
  data() {
    return {
      prefixCls,
      title: '',
      body: '',
      showChoose: false,
      selectGroup: {}
    }
  },
  watch: {
    body(val) {}
  },
  computed: {
    chooseTips() {
      return this.selectGroup.name || ''
    },
    groupID() {
      return this.selectGroup.id || this.$route.params.groupID
    },
    disabled() {
      return !(this.title.length > 0 && this.body.length > 0)
    }
  },
  methods: {
    showChoosePage() {
      this.showChoose = true
    },
    closeChoosePage() {
      this.showChoose = false
    },
    goBack() {
      this.$router.go(-1)
    },
    postFeed() {
      if (this.groupID) {
        const params = {
          title: this.title,
          body: this.body,
          summary: this.body,
          feed_from: 2
        }
        // /groups/:group/posts
        this.$http.post(`/plus-group/groups/${this.groupID}/posts`, {
          ...params
        }).then(({ data: { post } }) => {
          if (post.id) {
            console.log('')
            this.$router.push(`/group/${this.groupID}/feed/${post.id}`)
          }
        })
      } else {

      }
    }
  }
}

</script>
<style lang='less'>
@post-group-feed-prefix: post--group-feed;
.@{post-group-feed-prefix} {
  height: 100vh;
  overflow-y: auto;
  overflow-x: hidden;
  &-btn {
    font-size: 32px;
    background-color: #fff;
    color: #59b6d7;
    &[disabled] {
      color: #ccc
    }
  }
  &-select {
    display: flex;
    height: 100px;
    padding: 0 20px;
    font-size: 30px;
    align-items: center;
    justify-content: space-between;
    .v-icon {
      width: 24px;
      height: 24px;
      color: #ccc
    }
  }
  &-content {
    padding: 0 30px;
    height: calc(~'100vh -' 90px);
    background-color: #fff;
  }

  &-title {
    display: flex;
    align-items: conter;
    height: 100px;
    border-bottom: 1px solid #dedede;
    /*no*/
    input {
      font-size: 30px;
      padding: 0 20px;
      height: 100%;
      width: 100%;
    }
  }

  &-body {
    max-height: calc(~'100% -' 100px);
    overflow-y: scroll;
    textarea {
      font-size: 30px;
      padding: 20px;
      width: 100%;
      min-height: 400px;
      max-height: 100%;
      resize: none;
    }
  }
}

</style>



